.footer__logo {
  display: inline-block;
  background-image: url("../img/sprite.svg");
  background-size: 25.1875em 22.625em;
}

.footer {
  background-color: #F9F9F9;
  padding-top: 4rem;
  padding-bottom: 1rem;
}

.footer .about {
  margin-bottom: 30px;
}

.footer .adress {
  margin-bottom: 12px;
}

.footer li {
  padding-bottom: 18px;
}

.footer li::before {
  display: none;
}

.footer li a {
  color: #3C3C3C;
  border-bottom-color: transparent;
}

.footer li a:hover {
  color: #009EE3;
  border-bottom-color: #009EE3;
}

.footer__top {
  margin-bottom: 60px;
}

.footer__intro {
  display: none;
}

.footer__logo {
  width: 15.375em;
  height: 3.0625em;
  background-position: -0.3125em -9.3125em;
  margin-bottom: 42px;
  margin-top: 5px;
}

.footer__phone {
  margin-bottom: 1rem;
}

.footer__phone a {
  color: #373939;
  border-bottom: none;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
}

.footer__mail {
  margin-bottom: 16px;
}

.footer__mail a {
  font-size: 14px;
}

.footer__adress {
  padding-bottom: 18px;
}

.footer__links__service {
  order: 1;
}

.footer__adress-label {
  font-weight: bold;
}

.footer__bottom {
  padding-bottom: 13px;
  padding-bottom: 14px;
  flex-flow: row wrap;
  display: flex;
  justify-content: space-between;
}

.footer__bottom > * {
  flex: 0 1 auto;
}

@media only screen and (min-width: 768px) {
  .footer__top {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 5vw;
    margin-bottom: 125px;
  }

  .footer__intro {
    display: block;
  }

  .footer__adress {
    order: 2;
  }

  .footer__links__catalog {
    display: none;
  }
}

@media only screen and (min-width: 1366px) {
  .footer__top {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .footer__links__catalog {
    display: block;
  }
}